/* Variable declarations */
:root {
    --mainBlue: #006699;
    --mainGreen: #339966;
    --darkGray: #403B41;
    --softGray: #DEDEDE;
    --serif: 'David Libre', serif;
    --sansSerif: 'Arimo', sans-serif;
}

/* Overall styles - Starting points */
html {
    background-color: var(--softGray);
    color: var(--darkGray);
    font-family: var(--sansSerif);
}

body {
    min-width: 100vw;
    min-height: 100vh;
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--serif);
  margin: 0;
}

.section-title {
  padding: 2% 0 4%;
  text-align: center;
  font-size: 3rem;
}

section {
  padding: 3% 0;
}

/* Hero section */
.hero {
  margin-top: 25vh;
  min-height: 100vh;
}

.hero h1 {
  font-size: 4.5rem;
  text-align: center;
}

.break-line {
  width: 55vw;
  height: 1vh;
  background: linear-gradient(to right, var(--mainGreen), var(--mainBlue));
  border-radius: 5px;
  margin: 2% auto;
}

.intro-icons {
  display: flex;
  justify-content: center;
}

.intro-icons img {
  width: 7vw;
  padding: 0 1.5em;
}

.more {
  margin: 2% auto 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.more p {
  font-size: 1.3rem;
  margin: 1% 0 0;
}

.more img {
  width: 4vw;
  margin-top: -1%;
}

/* Project Style */
.project-section {
  color: var(--softGray);
  background-color: var(--darkGray);
}

.project-title {
  text-decoration: underline var(--mainGreen);
}

.project-block {
  width: 85vw;
  height: 70vh;
  margin: 1% auto;
  padding: 1%;
  border: 3px solid var(--mainGreen);
  display: grid;
  grid-template-columns: 40% 60%;
  grid-template-rows: 20% 50% 15% 15%;
  grid-column-gap: 1.5%;
  align-items: center;
}

.project-block:nth-child(even) {
  border: 3px solid var(--mainBlue);
}

.project-name {
  grid-column: 2;
  grid-row: 1;
  text-align: center;
  font-size: 2rem;
  text-decoration: underline var(--mainBlue);
}

.project-block:nth-child(even) .project-name {
  text-decoration: underline var(--mainGreen);
}

.project-images {
  grid-column: 1;
  grid-row: 1 / 5;
  align-self: stretch;
  justify-self: center;
  display: flex;
  flex-wrap: wrap;
}

.project-images img {
  max-width: 100%;
  max-height: 100%;
}

.multi-img img {
  max-width: 50%;
  max-height: 50%;
}

.project-desc {
  grid-column: 2;
  grid-row: 2;
  font-size: 1.25rem;
  padding: 0 2%;
}

.project-tech {
  grid-column: 2;
  grid-row: 3;
  color: var(--mainGreen);
  text-align: center;
}

.project-block:nth-child(even) .project-tech {
  color: var(--mainBlue);
}

.project-links {
  grid-column: 2;
  grid-row: 4;
  text-align: center;
}

.project-links a {
  color: var(--softGray);
  padding: 2%;
  font-size: 1.2rem;
  margin: 0 3%;
  text-decoration: underline var(--mainGreen);
  border: 1px solid var(--mainBlue);
}

.project-block:nth-child(even) .project-links a {
  text-decoration: underline var(--mainBlue);
  border: 1px solid var(--mainGreen);
}

/* Certificate Styles */
.certs {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  align-content: center;
  justify-content: space-around;
}

.cert-title {
  text-decoration: underline var(--mainBlue);
}

.cert-block {
  width: 40vw;
  border: 1.5px solid var(--darkGray);
  background-color: var(--darkGray);
}

.cert-block img {
  width: 100%;
  height: 100%;
}

.cert-caption {
  color: var(--softGray);
  text-align: center;
  padding: 2%;
}

/* About / Contact Styles */
.about-section {
  background-color: var(--darkGray);
  color: var(--softGray);
}

.about-title {
  text-decoration: underline var(--mainGreen);
}

